:root {
  --height-bar: 60px;
  --main-color: #00acdc;
}

body {
  margin: 0;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header {
  height: var(--height-bar);
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  margin: auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  height: var(--height-bar);
  color: #000;
  text-transform: uppercase;
  flex-direction: column;
  justify-content: center;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--main-color);
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

